class Header {
    selectors = {
        root: '[data-js-header]',
        overlay: '[data-js-header-overlay]',
        burgerButtonOpen: '[data-js-header-burger-button-open]',
        burgerButtonClose: '[data-js-header-burger-button-close]',
    };

    stateClasses = {
        isOpen: 'is-open',
        isLock: 'is-lock',
    };

    constructor() {
        this.rootElement = document.querySelector(this.selectors.root)
        this.overlayElement = this.rootElement.querySelector(this.selectors.overlay)
        this.burgerButtonOpenElement = this.rootElement.querySelector(this.selectors.burgerButtonOpen)
        this.burgerButtonCloseElement = this.rootElement.querySelector(this.selectors.burgerButtonClose)
        this.htmlTag = document.documentElement
        this.bindEvents()
    }

    onBurgerButtonClick = () => {
        this.overlayElement.classList.toggle(this.stateClasses.isOpen)
        this.htmlTag.classList.toggle(this.stateClasses.isLock)
    }

    bindEvents() {
        this.burgerButtonOpenElement.addEventListener('click', this.onBurgerButtonClick)
        this.burgerButtonCloseElement.addEventListener('click', this.onBurgerButtonClick)

    }
}

export default Header